import {Project} from '../../project';
import {development, regionalFairFinalist, awarded, nationalFairFinalist} from '../achievements';

export const alphaStudy: Project = {
  id: 1,
  title: 'AlphaStudy',
  team: 'Nathlia e Thas - PD15 (matutino)',
  advisor: 'Andria Cristina de Souza',
  mentors: ['Andr Lus dos Reis Gomes de Carvalho'],
  shortDescription: `Aplicativo de organizao do tempo dedicado aos estudos`,
  description: `Pensando na realidade e cotidiano dos alunos do Colgio Tcnico
  de Campinas, foi realizada uma pesquisa, com formulrio
  annimo, no referido local, com o objetivo de averiguar o perfil
  dos alunos e as reais necessidades que os mesmos enfrentam, ao
  se tratar da relao de organizao e planejamento da rotina de
  estudos. Com os resultados obtidos na pesquisa, foi possvel
  constatar que os mesmos enfrentam dificuldades para se
  organizarem e h a necessidade de uma ferramenta que possam
  auxili-los nesse processo. A partir dessas informaes, foi
  possvel compreender que o potencial pblico consumidor
  precisava de algo que os auxiliasse na gesto das tarefas dirias,
  ento iniciou-se o desenvolvimento de um aplicativo para
  smartphone Android, com as funcionalidades de Agenda e Plano
  de Estudo integradas, visando a praticidade e usabilidade dos
  usurios e tendo como objetivo principal, auxiliar no
  gerenciamento dos horrios e no desempenho acadmico do
  estudante em questo. Palavras Chave: Organizao; Agenda; Plano de Estudos;`,
  video: 'https://www.youtube.com/embed/qt9ZSYdbuRU',
  image: 'alpha-study.png',
  achievements: [
    development,
    regionalFairFinalist(['Bragantec 2017']),
    awarded(['3 lugar Informtica - Bragantec']),
    nationalFairFinalist(['Mostractc 2017']),
  ],
};



